<template>
  <div class="login-wrap">
    <div class="ms-login">
      <div class="login-title">注册</div>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
      >
        <el-form-item prop="username" label="用户名">
          <el-input
            v-model="ruleForm.username"
            placeholder="请输入用户名"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password" label="密码">
          <el-input
            type="password"
            placeholder="请输入密码"
            v-model="ruleForm.password"
          ></el-input>
        </el-form-item>
        <el-form-item prop="confirm" label="确认密码">
          <el-input
            type="password"
            placeholder="请输入密码"
            v-model="ruleForm.confirm"
          ></el-input>
        </el-form-item>
        <el-form-item prop="email" label="邮箱地址">
          <el-input
            type="text"
            placeholder="请输入邮箱地址"
            v-model="ruleForm.email"
          ></el-input>
        </el-form-item>
        <el-form-item prop="sex" label="性别">
          <el-select v-model="ruleForm.sex" placeholder="请选择活动区域">
            <el-option label="男" value="1"></el-option>
            <el-option label="女" value="0"></el-option>
          </el-select>
        </el-form-item>
        <div class="login-btn">
          <el-button type="success" @click="submitForm">立即注册</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mixin } from "../mixins";
import { getRegister } from "../api/index";
export default {
  mixins: [mixin],
  data: function () {
    return {
      yztoken: "",
      ruleForm: {
        username: "",
        password: "",
        confirm: "",
        email: "",
        sex: "",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        confirm: [
          { required: true, message: "请输入确认密码", trigger: "blur" },
        ],
        email: [{ required: true, message: "请输入邮箱", trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm() {
      if (
        this.ruleForm.password === this.ruleForm.confirm &&
        this.ruleForm.password != ""
      ) {
        getRegister({
          username:this.ruleForm.username,
          password:this.ruleForm.password,
          dpassword:this.ruleForm.confirm,
          email:this.ruleForm.email,
          sex:this.ruleForm.sex,
        }).then((res) => {
            console.log(res);
            if (res.code == 200) {
              this.$confirm("恭喜您，注册成功。点击“确定”进入登录页面", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "success",
                center: true,
              }).then(() => {
                this.$router.push("/");
              })
            } else {
              this.notify(res.msg?res.msg:"注册失败", "error");
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        if (this.ruleForm.password != "") {
          this.$confirm("密码不一致，请重新输入", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "error",
            center: true,
          })
            
        } else {
          this.$confirm("请完整输入表单内容！", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "error",
            center: true,
          })
            
        }
      }
    },
    toRegister() {
      this.$router.push("/register");
    },
  },
  created() {},
  mounted() {},
};
</script>

<style scoped>
.login-wrap {
  background: url("../assets/img/logo_bg.webp") no-repeat;
  background-size: 100% 100%;

  width: 100%;
  height: 100%;
}
.ms-logo img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.ms-login {
  position: absolute;
  left: calc(50% - 260px);
  top: 20%;
  width: 400px;
  margin: 0 0 0 0;
  padding: 40px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.4);
}
.login-btn {
  display: flex;
  justify-content: flex-end;
}
.login-btn button {
  width: 30%;
  height: 36px;
  margin-right: 10px;
}
.login-title {
  font-size: 24px;
  color: #fff;
  margin-top: -10px;
  padding-bottom: 20px;
  text-align: center;
  font-weight: bold;
}
::v-deep .el-form-item__label {
  color: #fff;
}
::v-deep .el-form-item {
  display: flex;
}
::v-deep .el-form-item__label {
  width: 80px;
}
::v-deep .el-input {
  width: 150%;
}

::v-deep .el-select .el-input {
  width: 140%;
}
</style>
